.FABMenu
{
    top:0;
    left:0;
    bottom:0;
    right:0;
    margin:auto;
    width:600px;
    height:50px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
}
.FABMenu input
{
    pointer-events: auto;
    position:absolute;
    top: -15%;
    left:50%;
    width:80px;
    height:80px;
    z-index:20;
    border-radius: 80px;
    opacity:0;
    cursor:pointer;
}
.hamburger
{
    position:absolute;
    top: -15%;
    left:50%;
    width: 80px;
    height:80px;
    background-color: #68a9fc;
    border-radius:100%;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.15);
    z-index:10;
    pointer-events: auto;
    cursor:pointer;
}
.input-tools:hover ~ .hamburger .dots span{
    background: lightgray;
}
.dots span
{
    position: absolute;
    top: 45%;
    width:8px;
    height:8px;
    border-radius:8px;
    background:#fff;
    z-index:20;
    transition: all .3s ease-in-out;
}

.first
{
    margin-left:1em;
}
.second
{

    margin-left:2.3em;
}
.third
{
    margin-left:3.6em;
}
.FABMenu input.tools-open ~ .hamburger .dots .first
{
    top:20px;
    height:60px;
    -webkit-transform-origin:top;
            transform-origin:top;
    -webkit-transform:rotate(-45deg);
            transform:rotate(-45deg);
    transition: all .3s ease-in-out;

}
.FABMenu input.tools-open ~ .hamburger .dots .third
{
    top:20px;
    height:60px;
    -webkit-transform-origin:top;
            transform-origin:top;
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
    transition: all .3s ease-in-out;
}
.action_items_bar
{
    position:absolute;
    top:4.5%;
    left:25%;
    width:380px;
    height:60px;
    background-color: #68a9fc;
    border-radius:60px;
    box-shadow: 0px 5px 20px #a2c9fa;
    z-index:5;

    /*   animation-name: expand-the-bar;
      animation-duration: 2s;
      animation-timing-function: ease-in-out; */
    -webkit-transform:scaleX(0);
            transform:scaleX(0);
    transition: all .3s ease-in-out;
}
.FABMenu input.tools-open ~ .action_items_bar
{
    -webkit-transform:scaleX(1);
            transform:scaleX(1);
}
.action_items span
{
    position: absolute;
    top: 17%;
    width:20px;
    padding-left:20px;
    pointer-events: auto;
    cursor:pointer;
}
.FABMenu-icon{
    font-size: 40px;
}
.action_items span .anticon:hover{
    color: lightgray;
    pointer-events: auto;
}
.first_item
{
    left:0%;
    padding-left:17px;
}
.second_item
{
    left:15%;
}
.third_item
{
    left:55%;
}
.fourth_item
{
    left:70%;
}
.FABMenu input.tools-open ~ .action_items_bar .action_items .first_item
{
    opacity:1;
    transition-delay:.45s;
}
.FABMenu input.tools-open ~ .action_items_bar .action_items .second_item
{
    opacity:1;
    transition-delay:.40s;

}
.FABMenu input.tools-open ~ .action_items_bar .action_items .third_item
{
    opacity:1;
    transition-delay:.40s;
}
.FABMenu input.tools-open ~ .action_items_bar .action_items .fourth_item
{
    opacity:1;
    transition-delay:.45s;
}

