.table {
  border: 1px solid #eee;
  table-layout: fixed;
  width: 100%;
  margin-bottom: 20px;
}
.table th {
  font-weight: bold;
  padding: 5px;
  background: #efefef;
  border: 1px solid #dddddd;
}
.table td {
  padding: 5px 10px;
  border: 1px solid #eee;
  text-align: left;
}
.table tbody tr:nth-child(odd) {
  background: #fff;
}
.table tbody tr:nth-child(even) {
  background: #f7f7f7;
}

.info-scroll {
  overflow: auto;
  height: 100vh;
  padding-bottom: 80px;
}

.info-scroll::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}

.info-scroll::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background-color: #aaa;
}
.info-scroll::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: #ddd;
}

